body{margin:0;padding:0;font-family:-apple-system, BlinkMacSystemFont, "Open Sans", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;box-sizing:border-box;font-size:18px;width:100%}*,*:before,*:after{box-sizing:inherit;font-size:inherit;font-family:inherit}.App{width:100%;max-width:700px}.modal-cover{position:fixed;top:0;left:0;width:100%;height:100%;z-index:10;transform:translateZ(0);background-color:rgba(0,0,0,0.8)}.modal-area{position:fixed;top:0;left:0;width:100%;height:100%;padding:2.5em 1.5em 1.5em 1.5em;background-color:#ffffff;box-shadow:0 0 10px 3px rgba(0,0,0,0.1);overflow-y:auto;-webkit-overflow-scrolling:touch}@media screen and (min-width: 500px){.modal-area{left:50%;top:50%;height:auto;transform:translate(-50%, -50%);max-width:30em;max-height:calc(100% - 1em)}}._modal-close{position:absolute;top:0;right:0;padding:0.5em;line-height:1;background:#f6f6f7;border:0;box-shadow:0;cursor:pointer}._modal-close-icon{width:25px;height:25px;fill:transparent;stroke:black;stroke-linecap:round;stroke-width:2}.modal-body{padding-top:0.25em}.modal-body .header{margin-bottom:10px}.modal-body label{margin-bottom:10px}.modal-body .form-control{margin-left:10px;margin-bottom:10px}._hide-visual{border:0 !important;clip:rect(0 0 0 0) !important;height:1px !important;margin:-1px !important;overflow:hidden !important;padding:0 !important;position:absolute !important;width:1px !important;white-space:nowrap !important}.scroll-lock{overflow:hidden;margin-right:17px}


.dates-list{list-style:none;padding-left:5px}.dates-list .thumb-down img,.dates-list .thumb-up img{width:2em;height:2em}.dates-list .date-vote{display:flex;flex-direction:row;justify-content:flex-start}.dates-list .date-vote .shown-date{align-self:flex-end;padding-bottom:7px;padding-right:0.5em}.dates-list .date-vote .thumb-up{position:relative}.dates-list .date-vote .thumb-up .data-rate{font-size:12px;border-radius:50%;background-color:lightgrey;position:absolute;top:-6px;right:-12px;padding:2.5px 3px}

.btn{box-sizing:border-box;padding:5px 5px;margin-right:5px;width:130px;border:0;background-color:white;border:1px solid black;color:black;border-radius:4px;transition:all 250ms ease-in-out;cursor:pointer}.btn:hover{background-color:#c6bcb6}.btn:focus{outline:none}li{list-style:none}

